.contact {
    margin: 0 auto;
    margin-top: 20px;
    padding: 15px 20px;
}

.contactText {
    padding: 5px;
    width: 250px;
}

.contactText:hover {
    cursor: pointer;
    color: blue;
}

.contactItem {

    min-width: 300px;
    list-style: none;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.contactIcon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.contactIcon:nth-child(3n) {
    cursor: pointer;
    color: rgb(239, 2, 2);
}

.contactIcon:nth-child(3n) {
    width: 40px;
    height: 40px;
    margin-left: 15px;
}